.App {
  text-align: center;
  vertical-align: middle;
}

li {
  border-bottom: solid 1px rgb(158, 158, 158);
  text-align: left;
  padding: 10px 0;
}

.album{
  font-size: 36px;
  display: inline-block;
  width: 64px;
  padding: 15px;
  margin-right: 10px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
